.login-page {
  width: 30%;
  margin: 5px auto;
  height: fit-content;
  border: 2px solid gray;
  padding: 25px;
}

.logo4 {
  height: "80px";
  width: "120px";
  text-align: center;

  margin: "2px 2px 2px 0px";
}

.logo-main {
  background-color: white;
}

.logo-login {
  text-align: center;
}

.login-form {
  display: flex;
  flex-direction: column;
  width: 75%;
  margin: auto;
}

.error {
  color: red;
  text-align: center;
}

.login-form button {
  color: white;
  background: rgb(243, 149, 26);

  border-radius: 5px;
  border: 0;
  color: white;
  height: 38px;
  width: 90px;
  margin: 10px auto;
}

.login-form label {
  margin: 5px 0px 5px 0px;
}

.login-form input {
  border-radius: 5px;
  height: 25px;
  margin: 5px 0px 5px 0px;
}

.already {
  margin-top: 20px;
  text-align: center;
  color: black;
}
