.side-container {
  line-height: normal;
  display: flex;
  flex-direction: column;
}

.side-container button {
  line-height: 1.8;
  font-size: 15px;

  border-radius: 6px;
  width: fit-content;
  padding: 10px;
  margin: 2px 3px;
  border-style: solid;

  border: 1px solid rgb(175, 175, 175);
  background-color: rgb(22, 21, 94);

  color: white;
}

.side-container a:hover {
  background-color: gray;
  color: whitesmoke;
}
