.main-header {
  color: white;
  display: flex;
  position: fixed;
  width: 100%;
  height: 40px;
  z-index: 1;
  align-items: center;
  background-color: rgb(22, 21, 94);
}

/* #rcrm-id {
  height: 10px;
} */

.main-header .logo:first-child {
  margin-right: auto;
}

.main-header .links-social {
  display: inline-block;
  padding: 0 25px 0 0px;
}

.main-header .links-social a {
  color: white;
}
.main-header .links-social a:hover {
  background-color: gray;
}

.logo {
  padding: 0 0 0 25px;
  font-size: x-large;
}
